.friendList {
  & .item {
    display: flex;
    align-items: center;
    justify-content: left;
    width: 300px;
    border: solid 1px rgba(128, 128, 128, 0.6);
    border-radius: 5px;
    margin: 20px;
    padding: 10px;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, .4);
    & .statusOn {
      border-radius: 50%;
      border: 2px solid rgba(0, 128, 0, .6);
      width: 10px;
      height: 10px;
      text-align: center;
      position: relative;
      animation: anim-glow-green 2s ease infinite;
      margin: 0 20px 0 10px;
    }
    & .statusOff {
      border-radius: 50%;
      border: 2px solid rgba(255, 0, 0, .6);
      width: 10px;
      height: 10px;
      text-align: center;
      position: relative;
      animation: anim-glow-red 2s ease infinite;
      margin: 0 20px 0 10px;
    }
    & .avatar {
        width: 40px;
        border: solid 1px rgba(128, 128, 128, 0.4);
        border-radius: 5px;
        box-shadow: 2px 2px 10px rgba(0, 0, 0, .4);
    }
    & .name {
        font-weight: 600;
        margin-left: 20px;
    }
  }
}

@keyframes anim-glow-green {
  0% {
    box-shadow: 0 0 rgba(rgba(0, 128, 0, .6), 1);
  }
  100% {
    box-shadow: 0 0 10px 8px transparent;
    border-width: 2px;
  }
}

@keyframes anim-glow-red {
  0% {
    box-shadow: 0 0 rgba(rgba(255, 0, 0, .6), 1);
  }
  100% {
    box-shadow: 0 0 10px 8px transparent;
    border-width: 2px;
  }
}
